<template>
  <yd-layout class="homeBox">
    <img src="../../../static/images/workCenter/bg@2x.png">
    <div class="search" :class="{active: searchFlag}">
      <input class="searchWord" type="text" v-model="applicationName" @focus="searchFlag = true" :placeholder="placeText">
      <img src="../../../static/images/letter@2x.png" class="letter" v-show="!searchFlag" @click="message=false">
      <div class="redDot" v-show="!searchFlag" v-if="message"></div>
      <span v-show="searchFlag" @click="searchFlag = false;applicationName=''" class="cancel">取消</span>
      <div v-show="searchFlag" @click="applicationName='';" class="close">
        <img src="../../../static/images/delete@3x.png" alt="">
            </div>
      </div>
      <div v-if="!searchFlag" v-show="isScrollBox" class="scrollBox" :class="{'active' : loadAll}">
        <div class="scrollText" :class="{'active' : loadAll}">
          <div :class="{'active': pullText == '拼命刷新中...'}" class="ball-pulse-sync">
            <div></div>
            <div></div>
            <div></div>
          </div>
          <p>{{ pullText }}</p>
        </div>
      </div>
      <div class="outerScroller" :class="{'active': loadAll}">
        <div class="scroll" ref="scroll">
          <transition name="fade">
            <div class="mainContent" ref="" v-show="!searchFlag">
              <div class="waitHandle">
                <div class="handleCot">
                  <yd-flexbox class="handleBottom">
                    <yd-flexbox-item @click.native="$router.push('/handle')">
                      <div class="handleTop">
                        <img src="../../../static/images/workCenter/waitHandle@3x.png" alt="">
                        <span class="redNum">12</span>
                      </div>
                      <p>待办</p>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                      <div class="handleTop">
                        <img src="../../../static/images/workCenter/jixiao@2x.png" alt="">
                        <span  class="redNum">13</span>
                      </div>
                      <p>绩效</p>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                      <div class="handleTop">
                        <img src="../../../static/images/workCenter/meeting.png" alt="">
                        <span class="redNum">14</span>
                      </div>
                      <p>会议</p>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                      <div class="handleTop handleDate">
                        <img src="../../../static/images/workCenter/mySchedule.png" alt="">
                        <span class="redNum">15</span>
                      </div>
                      <p handleTit>日程</p>
                    </yd-flexbox-item>
                  </yd-flexbox>
                </div>
              </div>
              <div class="banner">
                <yd-slider speed="1000" autoplay="3000" pagination-color="#ffffff" :pagination-activecolor="'#4D8CFB'">
                  <yd-slider-item>
                    <img src="../../../static/images/workCenter/yyzx.png">
                </yd-slider-item>
                    <yd-slider-item>
                      <img src="../../../static/images/workCenter/cpty.png">
                </yd-slider-item>
                      <yd-slider-item>
                        <img src="../../../static/images/workCenter/jx.png">
                </yd-slider-item>
                </yd-slider>
              </div>
              <div class="application">
                <div class="applicationBox">
                  <div class="swiper-container">
                    <div class="swiper-wrapper">
                      <div class="swiper-slide">
                        <ul class="appBox">
                          <li v-for="(item, index) in myApp" :key="index">
                            <div class="itemBox">
                              <img :src="'../../../static/images/workCenter/'+item.img">
                              <p v-text="item.name"></p>
                            </div>
                          </li>
                          <li @click="$router.push('/appCenter')">
                            <div class="itemBox more">
                              <img src="../../../static/images/workCenter/more@2x.png">
                              <p v-text="'更多'"></p>
                            </div>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="report">
                <div class="swiper-container2">
                  <div class="swiper-wrapper">
                    <div class="swiper-slide slide01" :class="{slide03:slideFlag}">
                      <div class="reportCot">
                        <div class="reportT">
                          <p class="fl">员工产品体验</p>
                          <p class="fr">本季度</p>
                        </div>
                        <yd-flexbox class="reportPieBox">
                          <yd-flexbox-item>
                            <div class="reportPie" id="js_echarts1"></div>
                            <div class="detailNum">
                              <div class="offText">线下交易</div>
                              <div class="offNum">16笔</div>
                            </div>
                            <div class="pieTit">
                              <span class="gray"></span>
                              <p>已达标</p>
                            </div>
                          </yd-flexbox-item>
                          <yd-flexbox-item>
                            <div class="reportPie" id="js_echarts2"></div>
                            <div class="detailNum">
                              <div class="offText">线上交易</div>
                              <div class="offNum">17笔</div>
                            </div>
                            <div class="pieTit">
                              <span class="red"></span>
                              <p>未达标</p>
                            </div>
                          </yd-flexbox-item>
                          <yd-flexbox-item>
                            <div class="reportPie" id="js_echarts3"></div>
                            <div class="detailNum detailNum2">
                              <div class="offText">APP内支付</div>
                              <div class="offNum">18笔</div>
                            </div>
                            <div class="pieTit">
                              <span class="gray"></span>
                              <p>已达标</p>
                            </div>
                          </yd-flexbox-item>
                        </yd-flexbox>
                      </div>
                    </div>
                    <div class="swiper-slide slide02" :class="{slide04:slideFlag}">
                      <div class="mainReport">
                        <div class="mainTop">
                          <div class="mainLeft">人事报表</div>
                          <div v-show="false" class="mainRight">详细</div>
                        </div>
                        <div class="mainCot">
                          <div class="circleBox">
                            <div class="circle" id="js_hrCircle"></div>
                            <div class="unshow"></div>
                            <div class="cirTitle">
                              <ul>
                                <li>
                                  <span class="cir01"></span>正常在岗
                                </li>
                                <li>
                                  <span class="cir02"></span>出差人员
                                </li>
                                <li>
                                  <span class="cir03"></span>请假人员
                                </li>
                              </ul>
                            </div>
                          </div>
                          <div class="circleRight">
                            <yd-flexbox class="topCir">
                              <yd-flexbox-item>
                                <h3>出差</h3>
                                <h3>人员</h3>
                              </yd-flexbox-item>
                              <yd-flexbox-item>
                                <h4>19</h4>
                                <p>本日</p>
                              </yd-flexbox-item>
                              <yd-flexbox-item>
                                <h4>20</h4>
                                <p>本周</p>
                              </yd-flexbox-item>
                            </yd-flexbox>
                            <yd-flexbox>
                              <yd-flexbox-item>
                                <h3>请假</h3>
                                <h3>人员</h3>
                              </yd-flexbox-item>
                              <yd-flexbox-item>
                                <h4>21</h4>
                                <p>本日</p>
                              </yd-flexbox-item>
                              <yd-flexbox-item>
                                <h4>22</h4>
                                <p>本周</p>
                              </yd-flexbox-item>
                            </yd-flexbox>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="bottomLine gotop">应用接入请联系</div>
              <div class="linebox">———————&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;———————</div>
              <div class="bottomLine gobottom">联系方式 18888888888</div>
            </div>
          </transition>
        </div>
      </div>
      <div class="searchPage" v-show="searchFlag">
        <dl v-if="applicationName==''" class="searchRec">
          <dt>搜索推荐</dt>
          <dd v-for="(item,index) in myApp" :key="index">
            <yd-flexbox v-if="index<4">
              <img :src="'../../../static/images/workCenter/'+item.img">
              <p>
                <span class="tit" v-text='item.name'></span>
              </p>
            </yd-flexbox>
          </dd>
        </dl>
      </div>
      <yd-popup v-model="centerShow" position="center" width="6rem" height="4.73rem" class="ydPopup">
        <div>
          <div style="height:1.1rem;background-color:#fff;" class="popup">
            已完成功能
          </div>
          <div class="details" style="background-color:#fff;">
            <div>首页：（下拉刷新）</div>
            <div>代办：点击代办（上拉加载）</div>
            <div>应用中心：点击更多（编辑应用，数据存储在vuex中）</div>
          </div>
          <div class="return" @click="centerShow=false">
            <img src="../../../static/images/close@2x.png">
                </div>
          </div>
      </yd-popup>
  </yd-layout>
</template>
<script>
import echarts from 'echarts';
import { mapGetters, mapMutations } from 'vuex';
export default {
  name: 'workCenter',
  data() {
    return {
      centerShow: true,
      searchFlag: false,
      placeText: '页面下拉可以刷新待办任务哦，试试吧',
      message: false,
      applicationName: '',
      slideFlag: false,
      pullText: '下拉刷新',
      loadAll: false,
      isScrollBox: false,
      myApp: [],
      myApp2: [{
        img: 'a1.png',
        name: '公司公告'
      }, {
        img: 'a2.png',
        name: '请休假'
      }, {
        img: 'a3.png',
        name: '绩效管理'
      }, {
        img: 'a4.png',
        name: '未接来电'
      }, {
        img: 'a5.png',
        name: '智能开票'
      }, {
        img: 'a6.png',
        name: '数据经纬'
      }, {
        img: 'a7.png',
        name: '差率费用标准'
      }],
      offItem: {
        id: 'js_echarts1',
        offPay: 20,
        codePay: 10,
        offPayColor1: '#9483ff',
        offPayColor2: '#2caaed',
        codePayColor1: '#88ddff',
        codePayColor2: '#38ccff'
      },
      onItem: {
        id: 'js_echarts2',
        offPay: 20,
        codePay: 10,
        offPayColor1: '#ff548b',
        offPayColor2: '#ffa813',
        codePayColor1: '#f9e517',
        codePayColor2: '#ffba60'
      },
      appItem: {
        id: 'js_echarts3',
        offPay: 20,
        offPayColor1: '#9e51ff',
        offPayColor2: '#f72671',
        codePayColor1: '#f27af6',
        codePayColor2: '#ff77c5'
      },
      cricleNum: {
        xianxiaerweima: 0,
        xianxiashanfu: 0,
        xianshangshanfu: 0,
        appkongjianzhifu: 0,
        appneizhifu: 0
      }
    }
  },
  computed: {
    ...mapGetters(['getAppCenter'])
  },
  mounted() {
    let vm = this;
    if (vm.getAppCenter.myApp.length === 0) {
      vm.myApp = vm.myApp2;
      vm.setAppCenter({
        myApp: vm.myApp
      });
    } else {
      vm.myApp = vm.getAppCenter.myApp;
    }
    vm.draw(vm.offItem);
    vm.draw(vm.onItem);
    vm.draw(vm.appItem);
    vm.otherPie();
    vm.loadSwiper2();
    vm.pullFun();
  },
  methods: {
    ...mapMutations([
      'setAppCenter'
    ]),
    draw(item) {
      let myChart = echarts.init(document.getElementById(item.id));
      myChart.setOption({
        series: [{
          name: '访问来源',
          type: 'pie',
          radius: ['77%', '100%'],
          avoidLabelOverlap: false,
          hoverAnimation: false,
          legendHoverLink: false,
          labelLine: {
            normal: {
              show: false
            }
          },
          data: [
            { value: item.offPay },
            { value: item.codePay }
          ]
        }],
        color: [{
          type: 'linear',
          x: 0,
          y: 0,
          x2: 0,
          y2: 1,
          colorStops: [{
            offset: 0,
            color: item.offPayColor1 // 0% 处的颜色
          }, {
            offset: 1,
            color: item.offPayColor2 // 100% 处的颜色
          }],
          globalCoord: false
        }, {
          type: 'linear',
          x: 0,
          y: 0,
          x2: 0,
          y2: 1,
          colorStops: [{
            offset: 0,
            color: item.codePayColor1
          }, {
            offset: 1,
            color: item.codePayColor2
          }],
          globalCoord: false
        }]
      });
    },
    otherPie() {
      let myChart2 = echarts.init(document.getElementById('js_hrCircle'));
      myChart2.setOption({
        series: [{
          name: '访问来源',
          type: 'pie',
          // animation: false,
          clickable: false,
          calculable: false,
          smooth: true,
          radius: ['65%', '90%'], // 内环和外环
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: true,
              position: 'center',
              color: '#333'
            },
            color: 'red'
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: [{ value: 19 }, { value: 20 }, { value: 21 }]
        }],
        color: [{
          type: 'linear',
          x: 0,
          y: 0,
          x2: 1,
          y2: 0,
          colorStops: [{
            offset: 0,
            color: '#9159f1' // 0% 处的颜色
          }, {
            offset: 1,
            color: '#55c1ff' // 100% 处的颜色
          }],
          globalCoord: false
        }, {
          type: 'linear',
          x: 0,
          y: 0,
          x2: 1,
          y2: 0,
          colorStops: [{
            offset: 0,
            color: '#6bd5ff' // 0% 处的颜色
          }, {
            offset: 1,
            color: '#38e5ff' // 100% 处的颜色
          }],
          globalCoord: false
        }, {
          type: 'linear',
          x: 0,
          y: 0,
          x2: 1,
          y2: 0,
          colorStops: [{
            offset: 0,
            color: '#b86bff' // 0% 处的颜色
          }, {
            offset: 1,
            color: '#f438ff' // 100% 处的颜色
          }],
          globalCoord: false
        }]
      })
    },
    // swiper插件(数据报表、产品体验)
    loadSwiper2() {
      let vm = this;
      vm.$nextTick(() => {
        this.galleryTop = new Swiper('.swiper-container2', {
          initialSlide: 0,
          autoplay: 5000,
          speed: 1000,
          // slidesPerView: 1,
          paginationClickable: true,
          observeParents: true, // 修改swiper的父元素时，自动初始化swiper
          pagination: '.swiper-pagination2',
          uniqueNavElements: false,
          onSlideChangeStart: function(swiper) {
            vm.slideFlag = !vm.slideFlag;
          }
        })
      });
    },
    pullFun() {
      const vm = this;
      let scroll = document.querySelector('.scroll');
      let outerScroller = document.querySelector('.outerScroller');
      let scrollview = document.querySelector('.yd-scrollview');
      // 防止ios头默认下拉
      document.body.addEventListener('touchmove', vm.scrollerFun, false);
      scrollview.addEventListener('touchmove', vm.scrollerFun, false);
      outerScroller.addEventListener('touchstart', function(event) {
        vm.loadAll = false;
        vm.pullText = '下拉刷新';
        var touch = event.targetTouches[0];
        // 把元素放在手指所在的位置
        vm.touchStart = touch.pageY;
      }, false);
      outerScroller.addEventListener('touchmove', function(event) {
        var touch = event.targetTouches[0];
        let allInt;
        // 确定向上滚动的最大值
        // 增加摩擦力  100以后就停止下拉  所以摩擦力的分母就为100
        let int = scroll.offsetTop / 100; // 以滚动距离为因子
        if (scroll.offsetTop > 0) {
          allInt = (1 - int * int * int) * 0.4;
        } else {
          allInt = 1;
        }
        if (scroll.offsetTop >= 60) {
          vm.isScrollBox = true;
        }
        let scrollJuli = scroll.offsetTop + (touch.pageY - vm.touchStart) * allInt;
        if ((scrollJuli) <= -(scroll.offsetHeight - outerScroller.offsetHeight)) {
          scroll.style.top = -(scroll.offsetHeight - outerScroller.offsetHeight) + 'px';
        } else if ((scrollJuli) >= 100) {
          // 下拉最大值为100
          scroll.style.top = '100px';
        } else {
          scroll.style.top = scrollJuli + 'px';
        }
        // 超过60px开始刷新
        if ((scrollJuli) > 60) {
          vm.pullText = '松开加载';
        } else {
          vm.pullText = '下拉刷新';
        }
        vm.touchStart = touch.pageY;
        vm.touchDis = touch.pageY - vm.touchStart;
      }, false);
      outerScroller.addEventListener('touchend', function(event) {
        clearInterval(vm.timer);
        clearInterval(vm.timer2);
        clearInterval(vm.timer3);
        vm.touchStart = 0;
        var top = scroll.offsetTop;
        // 松开加载
        vm.pullText = '拼命刷新中...';
        // 超过加载距离开始加载
        if (top >= 60) {
          vm.timer = setInterval(function() {
            scroll.style.top = scroll.offsetTop - 5 + 'px';
            if (scroll.offsetTop <= 40) {
              clearInterval(vm.timer);
              clearInterval(vm.timer2);
              clearInterval(vm.timer3);
              scroll.style.top = 40;
              // 数据加载完成后恢复
              vm.timer3 = setTimeout(function() {
                refresh().then(() => {
                  vm.loadAll = true;
                  vm.pullText = '已成功为您更新待办任务'
                  vm.timer2 = setTimeout(function() {
                    scroll.style.top = 0;
                    vm.loadAll = false;
                    vm.isScrollBox = false;
                  }, 1700);
                });
              }, 800);
            }
          }, 2)
        }
        if (top >= 0 && top < 60) {
          vm.timer = setInterval(function() {
            scroll.style.top = scroll.offsetTop - 5 + 'px';
            if (scroll.offsetTop < 5) {
              scroll.style.top = 0;
              clearInterval(vm.timer);
            }
          }, 2);
        }
        if (top < 0) {
          scroll.style.top = top;
        }
        console.log(top);
        if (top < 60) {
          vm.loadAll = false;
          vm.isScrollBox = false;
        }
      }, false);

      function refresh() {
        // 这里是后台数据
        let myPromise = new Promise((resolve, reject) => {
          setTimeout(function() {
            resolve(true);
          }, 2000)
        });
        return myPromise;
        // return vm.myPromise;
      }
    }
  }
}

</script>
<style lang="less" scoped>
@import "./workCenter.less";

.detailNum {
  position: absolute;
  top: 25%;
  left: 22%;
  font-size: 0.24rem;
  color: #333;

  .offNum {
    text-align: center;
  }
}

.detailNum2 {
  left: 18%;
}

.reportPieBox .yd-flexbox-item {
  position: relative;
}

.searchWord {
  width: 88%;
}

.letter {
  position: absolute;
  top: 0.63rem;
  right: 0.25rem;
  width: 0.6rem;
  height: 0.54rem;
}

.redDot {
  content: "";
  width: 0.12rem;
  height: 0.12rem;
  border-radius: 50%;
  background-color: #ff4a4a;
  position: absolute;
  top: 0.58rem;
  right: 0.28rem;
  z-index: 1;
}

.handleTit {
  padding-right: .05rem;
}

.more {
  p {
    padding-right: .1rem;
  }
}

.return {
  text-align: center;
  width: .74rem;
  border-radius: 50%;
  background: #bababa;
  height: .74rem;
  margin: 0 auto;
  margin-top: .2rem;
  padding-top: .23rem;

  img {
    width: .3rem;
    height: .3rem;
  }
}

.popup {
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  height: 1.1rem;
  line-height: 1.1rem;
  color: #333;
  font-size: .36rem;
  text-align: center;
  border-bottom: 1px dashed #eaeaea;
}

.details {
  padding-left: .54rem;
  padding-right: .54rem;
  padding-top: .25rem;
  font-size: .3rem;
  color: #666;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  padding-bottom: .2rem;
  line-height: .6rem;
}

</style>



